import { createReducer } from '@reduxjs/toolkit';
import { Car, CarsState } from '../../types/Cars';
import {
  clearPostCarData,
  postCar,
  postCarError,
  postCarSuccess,
  requestCar, requestCarError,
  requestCars,
  requestCarsError,
  requestCarsSuccess, requestCarSuccess
} from './actions';

const carsReducer = createReducer<CarsState>({
  cars: null,
  loading: false,
  error: null,
  postCar: null,
  postLoading: false,
  postError: null,
  carById: null,
  carByIdLoading: false,
  carByIdError: null,
}, {
  [requestCars.type]: (state) => ({
    ...state,
    cars: null,
    loading: true,
    error: null,
  }),
  [requestCarsSuccess.type]: (state, { payload }) => ({
    ...state,
    cars: payload,
    loading: false,
    error: null,
  }),
  [requestCarsError.type]: (state, { payload }) => ({
    ...state,
    cars: null,
    loading: false,
    error: payload,
  }),
  [requestCar.type]: (state) => ({
    ...state,
    carById: null,
    carByIdLoading: true,
    carByIdError: null,
  }),
  [requestCarSuccess.type]: (state, { payload }) => ({
    ...state,
    carById: payload,
    carByIdLoading: false,
    carByIdError: null,
  }),
  [requestCarError.type]: (state, { payload }) => ({
    ...state,
    carById: null,
    carByIdLoading: false,
    carByIdError: payload,
  }),
  [postCar.type]: (state) => ({
    ...state,
    postCar: null,
    postLoading: true,
    postError: null,
  }),
  [postCarSuccess.type]: (state, { payload }) => ({
    ...state,
    postCar: payload,
    postLoading: false,
    postError: null,
  }),
  [postCarError.type]: (state, { payload }) => ({
    ...state,
    postCar: null,
    postLoading: false,
    postError: payload,
  }),
  [clearPostCarData.type]: (state, { payload }) => ({
    ...state,
    postCar: null,
    postLoading: false,
    postError: null,
  }),
});

export default carsReducer;
